import React, { Fragment } from "react"
import { Link } from "gatsby"
import {publicationInfoList} from "../contents/publication-info";
import PublicationItem from "./publication-item";

// (pub) => {return pub.tags.indexOf(projectTag) >= 0;}
const PublicationSelection = ({projectFilterFunction: publicationFilterFunction}) => (
    <div className="content">
        <div className="content has-text-justified">
        {
                publicationInfoList.filter(publicationFilterFunction).sort((pubA, pubB) => {return pubB.year - pubA.year;}).map((publication, index) => {
                    return <div key={index}>
                        <PublicationItem publication={publication}></PublicationItem>
                        <br />
                        <br />
                    </div>
                })
            }

        </div>
    </div>
)

export default PublicationSelection